@mixin clearfix {
  content: '.';
  clear: both;
  display: block;
  height: 0;
  visibility: hidden;
}

@mixin background-image(
    $image,
    $grDir: to bottom,
    $opStart:0.3,
    $opEnd:0.5,
    $bgPos: center center
  ) {
  background:  linear-gradient(
      $grDir,
      rgba(0, 0, 0, $opStart),
      rgba(0, 0, 0, $opEnd)
    ), url($image);
  background-repeat: no-repeat;
  background-position: $bgPos;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}
